<template>
  <div class="add-goods">
      <p><label for="">商品名称：</label><input v-model="title" type="text"></p>
      <p><label for="">商品价格：</label><input v-model="price" type="text"></p>
      <p><label for="">商品描述：</label><input v-model="desc" type="text"></p>
      <div class="submit"><span @click="submitAction">提交</span><span @click="cancelAction">清空</span></div>
  </div>
</template>

<script>
import { http, api } from "../../request";

export default {
  data(){
    return {
      title: '',
      price: '',
      desc: ''
    }
  },
  methods: {
    async submitAction(){
      const data = await http.post(api.ADD_GOODS_API, {
        title: this.title,
        price: this.price,
        desc: this.desc,
      });
      this.title = '';
      this.price = '';
      this.desc = '';
      alert('添加成功')
    },
    cancelAction(){
      this.title = '';
      this.price = '';
      this.desc = '';
    }
  }
}
</script>

